import { Component, Inject, OnInit, OnChanges, NgZone, Output, Input, EventEmitter, SimpleChanges } from '@angular/core';
import { AuthService, ApiService, ContextService } from 'sb-shared-lib';
import { Router, ActivatedRoute } from '@angular/router';

import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { MatAutocompleteSelectedEvent} from '@angular/material/autocomplete';

import { Observable, ReplaySubject, BehaviorSubject } from 'rxjs';

import { find, map, mergeMap, startWith, debounceTime } from 'rxjs/operators';

import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';

interface DialogCreatePartnerData {
  owner_identity_id: number;
  relationship: string;
}

interface vmModel {
  customer: {
    name:           string,
    inputClue:      ReplaySubject<any>,
    filteredList:   Observable<any>,
    inputChange:    (event:any) => void,
    change:         (event:MatAutocompleteSelectedEvent) => void,
    focus:          () => void,
    restore:        () => void,
    reset:          () => void,
    display:        (customer:any) => string
  },
  payer: {
    name:           string
    inputClue:      ReplaySubject<any>,
    filteredList:   Observable<any>,
    inputChange:    (event:any) => void,
    change:         (event:any) => void,
    focus:          () => void,
    restore:        () => void,
    reset:          () => void,
    display:        (payer:any) => string
  }
}

@Component({
  selector: 'booking-edit-customer',
  templateUrl: 'booking.edit.customer.component.html',
  styleUrls: ['booking.edit.customer.component.scss']
})
export class BookingEditCustomerComponent implements OnInit  {

  @Input() bookingInput: ReplaySubject<any>;
  @Input() bookingOutput: ReplaySubject<any>;

  // reference to parent's booking object for sub-object creation
  private booking:any;

  // sub-objects (not present in booking)
  public customer:any;
  public payer:any;

  public has_payer:boolean;

  public vm: vmModel;

  constructor(
              private api: ApiService,
              private dialog: MatDialog,
              private zone: NgZone,
              private context:ContextService
              ) {

    this.customer = null;
    this.has_payer = false;
    this.payer = null;

    this.vm = {
      customer: {
        name:           '',
        inputClue:      new ReplaySubject(1),
        filteredList:   new Observable(),
        inputChange:    (event:any) => this.customerInputChange(event),
        change:         (event:MatAutocompleteSelectedEvent) => this.customerChange(event),
        focus:          () => this.customerFocus(),
        restore:        () => this.customerRestore(),
        reset:          () => this.customerReset(),
        display:        (customer:any) => this.customerDisplay(customer)
      },
      payer: {
        name:           '',
        inputClue:      new ReplaySubject(1),
        filteredList:   new Observable(),
        inputChange:    (event:any) => this.payerInputChange(event),
        change:         (event:any) => this.payerChange(event),
        focus:          () => this.payerFocus(),
        restore:        () => this.payerRestore(),
        reset:          () => this.payerReset(),
        display:        (payer:any) => this.payerDisplay(payer)
      }
    };

  }


  public ngOnInit() {

    /**
     * listen to the parent for changes on booking object
     */
    this.bookingInput.subscribe( async (booking: any) => {
      this.zone.run( async () => {
        try {
          console.log('BookingEditCustomerComponent: received change from parent, updating');

          this.booking = booking;

          this.has_payer = booking.has_payer_organisation;

          if(booking.customer_id) {
            let data:any = await this.api.read("identity\\Partner", [booking.customer_id], ["id", "name", "partner_identity_id"]);
            if(data && data.length) {
              let customer = data[0];
              this.customer = customer;
              this.vm.customer.name = customer.name;
            }  
          }

          if(booking.payer_organisation_id) {
            let data:any = await this.api.read("identity\\Partner", [booking.payer_organisation_id], ["id", "name", "partner_identity_id"]);
            if(data && data.length) {
              let payer = data[0];
              this.payer = payer;
              this.vm.payer.name = payer.name;
            }
          }
        }
        catch(response) {}
      });


    });


    // update filtered lists, based on input clues

    this.vm.customer.filteredList = this.vm.customer.inputClue.pipe(
      debounceTime(300),
      map( (value:any) => (typeof value === 'string' ? value : (value == null)?'':value.name) ),
      mergeMap( async (name:string) => this.filterCustomers(name) )
    );

    this.vm.payer.filteredList = this.vm.payer.inputClue.pipe(
      debounceTime(300),
      map( (value:any) => (typeof value === 'string' ? value : (value == null)?'':value.name) ),
      mergeMap( async (name:string) => this.filterPayers(name) )
    );

  }

  private async customerChange(event:MatAutocompleteSelectedEvent) {
    let customer = event.option.value;
    this.customer = customer;
    this.vm.customer.name = customer.name;
    this.bookingOutput.next({customer_id: this.customer.id});
  }

  private customerInputChange(event:any) {
    this.vm.customer.inputClue.next(event.target.value);
  }

  private customerFocus() {
    this.vm.customer.inputClue.next("");
  }

  private customerDisplay(customer: any): string {
    return customer ? customer.name : '';
  }

  private customerReset() {
    setTimeout( () => {
      this.vm.customer.name = '';
    }, 100);
  }

  private customerRestore() {
    if(this.customer) {
      this.vm.customer.name = this.customer.name;
    }
    else {
      this.vm.customer.name = '';
    }
  }

  private payerChange(event:any) {
    console.log('BookingEditCustomerComponent::payerChange', event);

    // from mat-slide-toggle
    if(event && event.hasOwnProperty('checked')) {
      this.has_payer = <boolean>event.checked;
      if(!this.has_payer) {
        this.bookingOutput.next({payer_organisation_id: 0});
      }  
    }
    // from mat-autocomplete
    else if(event && event.option && event.option.value) {
      let payer = event.option.value;
      this.payer = payer;
      this.vm.payer.name = payer.name;
      this.bookingOutput.next({payer_organisation_id: this.payer.id, has_payer_organisation: this.has_payer});
    }
    
  }

  private payerInputChange(event:any) {
    this.vm.payer.inputClue.next(event.target.value);
  }

  private payerFocus() {
    this.vm.payer.inputClue.next("");
  }

  private payerDisplay(payer: any): string {
    return payer ? payer.name : '';
  }

  private payerReset() {
    setTimeout( () => {
      this.vm.payer.name = '';
    }, 100);
  }

  private payerRestore() {
    if(this.payer) {
      this.vm.payer.name = this.payer.name;
    }
    else {
      this.vm.payer.name = '';
    }
  }


  /**
   * Request a new eQ context for selecting a payer, and relay change to self::payerChange(), if an object was created
   * #sb-booking-container is defined in booking.edit.component.html
   */
  public selectPayer() {
    console.log("BookingEditCustomerComponent::selectPayer");

    let descriptor = {
      context: {
        entity:     'identity\\Partner',
        type:       'form',
        name:       'payer',
        domain:     [ ['owner_identity_id', '=', this.customer.partner_identity_id], ['relationship', '=', 'payer'] ],
        mode:       'edit',
        purpose:    'create',
        target:     '#sb-booking-container',
        callback:   (data:any) => {
          if(data && data.objects && data.objects.length) {
            this.payerChange({option: {
              value: data.objects[0]
            }});
          }
        }
      }
    };

    this.context.change(descriptor);
  }

  public createPayer() {
    const dialogRef = this.dialog.open(DialogCreatePartner, {
      width: '80vw',
      data: {owner_identity_id: this.customer.partner_identity_id, relationship: 'payer'}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  /**
   * Search for Customers that match the name.
   *
   * @param name
   * @returns
   */
  private async filterCustomers(name: string) {
    let filtered:any[] = [];
    try {
      let data:any[] = await this.api.collect("identity\\Partner", [["name", "ilike", '%'+name+'%'], ["relationship", "=", "customer"]], ["id", "name"], 'name', 'asc', 0, 5);
      filtered = data;
    }
    catch(response) {
      console.log(response);
    }
    return filtered;
  }

  private async filterPayers(name: string) {
    let filtered:any[] = [];
    try {
      let data:any[] = await this.api.collect("identity\\Partner", [["name", "ilike", '%'+name+'%'], ["owner_identity_id", "=", this.customer.partner_identity_id], ["relationship", "=", "payer"]], ["id", "name"], 'name', 'asc', 0, 5);
      filtered = data;
    }
    catch(response) {
      console.log(response);
    }
    return filtered;
  }


}



@Component({
  selector: 'dialog-booking-edit-customer-create-partner-dialog',
  templateUrl: 'booking.edit.customer.create.partner.dialog.html'
})
export class DialogCreatePartner {

  constructor(
    public dialogRef: MatDialogRef<DialogCreatePartner>,
    @Inject(MAT_DIALOG_DATA) public data: DialogCreatePartnerData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}