import { Component, Inject, OnInit, OnChanges, NgZone, Output, Input, EventEmitter, SimpleChanges } from '@angular/core';
import { AuthService, ApiService, ContextService } from 'sb-shared-lib';
import { Router, ActivatedRoute } from '@angular/router';

import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { MatAutocompleteSelectedEvent} from '@angular/material/autocomplete';

import { Observable, ReplaySubject, BehaviorSubject } from 'rxjs';

import { find, map, mergeMap, startWith, debounceTime } from 'rxjs/operators';

import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { type } from 'jquery';

interface ContactModel {
  firstname:        string;
  lastname:         string;
  type:             string;
  email:            string;
  phone:            string;
  relationship:     string;
}

interface vmModel {
  center: {
    name:           string,
    inputClue:      ReplaySubject<any>,
    filteredList:   Observable<any>,
    inputChange:    (event:any) => void,
    change:         (event:MatAutocompleteSelectedEvent) => void,
    focus:          () => void,
    restore:        () => void,
    reset:          () => void,
    display:        (center:any) => string
  },
  type: {
    name:           string
    inputClue:      ReplaySubject<any>,
    filteredList:   Observable<any>,
    inputChange:    (event:any) => void,
    change:         (event:any) => void,
    focus:          () => void,
    restore:        () => void,
    reset:          () => void,
    display:        (type:any) => string
  },
  description: {
    value:          string,
    change:         (event:any) => void
  },
  contacts: {
    values:         Array<any>,
    fields:         Array<any>,
    list:           ReplaySubject<any>,
    create:         () => void,
    remove:         (id:number) => void
  }
}

@Component({
  selector: 'booking-edit-sojourn',
  templateUrl: 'booking.edit.sojourn.component.html',
  styleUrls: ['booking.edit.sojourn.component.scss']
})
export class BookingEditSojournComponent implements OnInit  {

  @Input() bookingInput: ReplaySubject<any>;
  @Input() bookingOutput: ReplaySubject<any>;

  // reference to parent's booking object for sub-object creation
  private booking:any;

  // sub-objects (not present in booking)
  public center:any;
  public type:any;
  public customer:any;

  public has_payer:boolean;

  public vm: vmModel;

  constructor(
              private api: ApiService,
              private auth: AuthService,
              private dialog: MatDialog,
              private zone: NgZone,
              private context:ContextService
              ) {

    this.booking = null;
    this.center = null;
    this.type = null;

    this.vm = {
      center: {
        name:           '',
        inputClue:      new ReplaySubject(1),
        filteredList:   new Observable(),
        change:         (event:MatAutocompleteSelectedEvent) => this.centerChange(event),
        inputChange:    (event:any) => this.centerInputChange(event),
        focus:          () => this.centerFocus(),
        restore:        () => this.centerRestore(),
        reset:          () => this.centerReset(),
        display:        (center:any) => this.centerDisplay(center)
      },
      type: {
        name:           '',
        inputClue:      new ReplaySubject(1),
        filteredList:   new Observable(),
        change:         (event:any) => this.typeChange(event),
        inputChange:    (event:any) => this.typeInputChange(event),
        focus:          () => this.typeFocus(),
        restore:        () => this.typeRestore(),
        reset:          () => this.typeReset(),
        display:        (type:any) => this.typeDisplay(type)
      },
      description: {
        value:          '',
        change:         (event:any) => this.descriptionChange(event)
      },
      contacts: {
        values:         [],
        fields:         ['name', 'type', 'email', 'phone', '_actions'],
        list:           new ReplaySubject(1),
        create:         () => this.createContact(),
        remove:         (id:number) => this.removeContact(id)
      }
    };

  }


  public ngOnInit() {
    this.vm.contacts.list.subscribe( (value) => {
      console.log("contact list: received new value", value);
    });

    /**
     * listen to the parent for changes on booking object
     */
    this.bookingInput.subscribe( async (booking: any) => {
      this.zone.run( async () => {
        try {
          console.log("BookingEditSojournComponent: received changes from parent", booking);

          this.booking = booking;

          if(booking.customer_id) {
            let data:any = await this.api.read("identity\\Partner", [booking.customer_id], ["id", "name", "partner_identity_id"]);
            if(data && data.length) {
              let customer = data[0];
              this.customer = customer;
            }  
          }

          if(booking.center_id) {          
            let data:any = await this.api.read("lodging\\identity\\Center", [booking.center_id], ["id", "name", "code", "organisation_id"]);
            if(data && data.length) {
              let center = data[0];
              this.center = center;
              this.vm.center.name = center.name;
            }
          }

          if(booking.type_id) {
            let data:any = await this.api.read("sale\\booking\\BookingType", [booking.type_id], ["id", "name", "code"]);
            if(data && data.length) {
              let type = data[0];
              this.type = type;
              this.vm.type.name = type.name;
            }
          }

          if(booking.description && booking.description.length) {
            this.vm.description.value = booking.description;
          }

          if(booking.contacts_ids) {
            let data:any = await this.api.read("sale\\booking\\Contact", booking.contacts_ids, ["id", "name", "type"]);
            console.log(booking.contacts_ids, data);
            if(data && data.length) {
              this.vm.contacts.values = data;
              this.vm.contacts.list.next(data);
            }
          }
          
        }
        catch(response) {}
      });


    });


    // update filtered lists, based on input clues

    this.vm.center.filteredList = this.vm.center.inputClue.pipe(
      debounceTime(300),
      map( (value:any) => (typeof value === 'string' ? value : (value == null)?'':value.name) ),
      mergeMap( async (name:string) => this.filterCenters(name) )
    );

    this.vm.type.filteredList = this.vm.type.inputClue.pipe(
      debounceTime(300),
      map( (value:any) => (typeof value === 'string' ? value : (value == null)?'':value.name) ),
      mergeMap( async (name:string) => this.filterTypes(name) )
    );

  }

  private async centerChange(event:MatAutocompleteSelectedEvent) {
    let center = event.option.value;
    this.center = center;
    this.vm.center.name = center.name;

    // relay change to parent component
    this.bookingOutput.next({center_id: this.center.id});
  }

  private centerFocus() {
    this.vm.center.inputClue.next("");
  }

  private centerInputChange(event:any) {
    this.vm.center.inputClue.next(event.target.value);
  }

  private centerDisplay(center:any): string {
    return center ? center.name: '';
  }

  private centerReset() {
    setTimeout( () => {
      this.vm.center.name = '';
    }, 100);
  }

  private centerRestore() {
    if(this.center) {
      this.vm.center.name = this.center.name;
    }
    else {
      this.vm.center.name = '';
    }
  }

  private descriptionChange(event:any) {
    console.log('BookingEditCustomerComponent::descriptionChange', event);
    let description = event.target.value;
    // relay change to parent component
    this.bookingOutput.next({description: description});
  }

  private typeChange(event:any) {
    console.log('BookingEditCustomerComponent::typeChange', event)

    // from mat-autocomplete
    if(event && event.option && event.option.value) {
      let type = event.option.value;
      this.type = type;
      this.vm.type.name = type.name;
      // relay change to parent component
      this.bookingOutput.next({type_id: type.id});
    }

  }

  private typeInputChange(event:any) {
    this.vm.type.inputClue.next(event.target.value);
  }

  private typeFocus() {
    this.vm.type.inputClue.next("");
  }

  private typeDisplay(type:any): string {
    return type ? type.name: '';
  }

  private typeReset() {
    setTimeout( () => {
      this.vm.type.name = '';
    }, 100);
  }

  private typeRestore() {
    if(this.type) {
      this.vm.type.name = this.type.name;
    }
    else {
      this.vm.type.name = '';
    }
  }

  /**
   * Permanently remove the contact (but not the identity) from the booking.
   * 
   * @param id 
   */
  private async removeContact(id:number) {
    try {
      const identity = await this.api.remove("sale\\booking\\Contact", [id], true);
      let index = this.vm.contacts.values.findIndex( (element) => element.id == id);
      this.vm.contacts.values.splice(index, 1);
      this.vm.contacts.list.next([...this.vm.contacts.values]);
    }
    catch(response) {
      console.warn(response);
    }
  }

private createContact() {
  let user = this.auth.getUser();

  let descriptor = {
    context: {
      entity:     'sale\\booking\\Contact',
      type:       'form',
      name:       'default',
      domain:     [ ['booking_id', '=', this.booking.id], ['owner_identity_id', '=', this.customer.partner_identity_id], ['relationship', '=', 'contact'], ['type', '=', 'booking'] ],
      mode:       'edit',
      purpose:    'create',
      target:     '#sb-booking-container',
      callback:   (data:any) => {
        if(data && data.objects && data.objects.length) {
            let contact = data.objects[0];
            let contacts_ids = this.vm.contacts.values.map( (elem) => elem.id );
            contacts_ids.push(contact.id);
            this.bookingOutput.next({contacts_ids: contacts_ids});
        }
      }
    }
  };

  this.context.change(descriptor);
}

  private alternateCreateContact() {
    const dialogRef = this.dialog.open(DialogCreateContact, {
      width: '80vw',
      data: { relationship: 'contact', type: 'booking' }
    });

    dialogRef.afterClosed().subscribe( async (data) => {
      if(data) {
        console.log(data);

        // 1) create a new identity, and get the ID 
        try {
          const identity = await this.api.create("identity\\Identity", {
            firstname: data.firstname,
            lastname: data.lastname,
            email: data.email,
            phone: data.phone
          });
          // 2) create a partner with customer_id from booking as owner_identity_id and new identity ID as partner_identity_id
          try {
            let user = this.auth.getUser();
            const contact = await this.api.create("sale\\booking\\Contact", {
              // owner is the organisation of current user
              owner_identity_id: user.organisation_id,
              partner_identity_id: identity.id,
              booking_id: this.booking.id,
              type: data.type
            });

            // update VM list of contacts
            this.vm.contacts.values.push({
              id: contact.id,
              name: data.firstname + ' ' + data.lastname,
              type: data.type,
              email: data.email,
              phone: data.phone
            });
              
            this.vm.contacts.list.next([...this.vm.contacts.values]);

          }
          catch(response) {
            console.warn(response);
          }

        }
        catch(response) {
          console.warn(response);
        }


      }
    });
  }

  /**
   * Search for Customers that match the name.
   *
   * @param name
   * @returns
   */
  private async filterCenters(name: string) {
    let filtered:any[] = [];
    // server will return only centers visible for current User
    try {
      let data:any[] = await this.api.collect("lodging\\identity\\Center", [["name", "ilike", '%'+name+'%']], ["id", "name"], 'name', 'asc', 0, 5);
      filtered = data;
    }
    catch(response) {
      console.log(response);
    }
    return filtered;
  }

  private async filterTypes(name: string) {
    let filtered:any[] = [];
    try {
      let data:any[] = await this.api.collect("sale\\booking\\BookingType", [["name", "ilike", '%'+name+'%']], ["id", "name"], 'name', 'asc', 0, 25);
      filtered = data;
    }
    catch(response) {
      console.log(response);
    }
    return filtered;
  }


}



@Component({
  selector: 'dialog-booking-edit-customer-create-contact-dialog',
  templateUrl: 'booking.edit.customer.create.contact.dialog.html'
})
export class DialogCreateContact {

  public form: FormGroup;

  get f() { return this.form.controls; }
    
  constructor(
    public dialogRef: MatDialogRef<DialogCreateContact>,
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) private data: ContactModel) {

      this.form = this.formBuilder.group({
        firstname: [this.data ? this.data.firstname : '', Validators.required],
        lastname: [this.data ? this.data.lastname : '', Validators.required],
        type: [this.data ? this.data.type : 'booking', Validators.required],
        email: [this.data ? this.data.email : '', [Validators.required,Validators.email]],
        phone: [this.data ? this.data.phone : ''],
      })
  }

  private markFormGroupTouched(formGroup: FormGroup) {
    (<any>Object).values(formGroup.controls).forEach( (control:any) => {
      control.markAsTouched();
      if (control.controls) {
        this.markFormGroupTouched(control);
      }
    });
  }

  onCancel(): void {
    this.dialogRef.close();
  }

  onSubmit() {
    // stop here if form is invalid
    if (this.form.invalid) {
      return this.markFormGroupTouched(this.form);
    }
    
    this.dialogRef.close({
      firstname: this.form.value.firstname,
      lastname: this.form.value.lastname,
      type: this.form.value.type,
      email: this.form.value.email,
      phone: this.form.value.phone  
    });

  }

}